import React, { Component } from 'react'
import { useSelector, useDispatch } from 'react-redux';
import { increaseone, decrease, increasetwo } from '../modules/counter';
import Test_1 from '../components/Test_1';

function TotalTest() {
    const page_count = useSelector(state => state.counter.page_count)

    return (
        <div>
            {page_count}
            <Test_1 />
        </div>
    )
}
export default TotalTest;