import React, { useEffect, useRef } from 'react'
import { Link } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux';
import { increaseone, decrease, increasetwo } from '../modules/counter';
import Buttons from './Buttons'
import Data from '../contentdata/Data.json'

function Test_1() {
    const page_count = useSelector(state => state.counter.page_count)
    const dispatch = useDispatch();
    const data = Data[page_count].text;
    return (
        <div>
            {data} 페이지입니다.
            <Buttons />
        </div>
    )
}

export default Test_1;
