function Buttons() {
    useEffect(() => {
        console.log(Element.current)
    })
    const dispatch = useDispatch();
    const onPlusOne = () => {
        dispatch(increaseone());
    }
    const onPlusTwo = () => {
        dispatch(increasetwo());
    }
    const onMinus = () => {
        dispatch(decrease());
    }
    return (
        <div>
            <button onClick={onPlusOne}>예스</button>
            <button onClick={onPlusTwo}>노</button>
            <button onClick={onMinus}>뒤로가기</button>
        </div>
    )
}
export default Buttons;