import React, { Component } from 'react'
import { Link } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux';
import { increaseone, decrease, increasetwo } from '../modules/counter';


function Progress() {
    const page_count = useSelector(state => state.counter.page_count)
    const wid = page_count * 40;
    const tempStyle = {
        display: "inline-block",
        width: `${wid}px`,
        height: "10px",
        boder: "1px solid black",
        background: "orange",
        transition: "all ease 0.2s"
    }


    return (
        <div style={tempStyle}>
            {page_count}
        </div>
    )
}
export default Progress;