const INCREASEONE = 'INCREASEONE';
const INCREASETWO = 'INCREASETWO';
const DECREASE = 'DECREASE';
export const increaseone = () => ({ type: INCREASEONE });
export const increasetwo = () => ({ type: INCREASETWO });
export const decrease = () => ({ type: DECREASE });

const init = {
    page_count: 0,
    total: 0,
    r_total: []
}

export default function counter(state = init, action) {
    switch (action.type) {
        case INCREASEONE:
            return {
                ...state,
                page_count: state.page_count + 1,
            }
        case INCREASETWO:
            return {
                ...state,
                page_count: state.page_count + 1,
            }
        case DECREASE:
            return {
                ...state,
                page_count: state.page_count - 1,
            }
        default:
            return state
    }
}